:root {
  --gray: #707070
}

.App {
  text-align: center;
  background-image: url("../resources/background.svg");
  background-position: center;
  background-size: auto;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100%;
  width: 100%;
}

h1 {
  font-family: Arial, sans-serif;
  font-size: 48pt;
  color: var(--gray);
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 20px;
}

h3 {
  font-family: Arial, sans-serif;
  font-size: 18pt;
  color: var(--gray);
  font-weight: normal;
}

p {
  font-size: larger;
  padding-top: 2em;
  text-align: left;
  opacity: 1;
}

#main-content-box {
  margin: 0px 20vw;
  background-color: rgba(255,255,255,.7);
  backdrop-filter: blur(6px);
  box-shadow: #00000080 0px 5px 5px 0;
  min-height: 100vh;
  padding: 40px;
}

#header {
  display: flex;
  padding-bottom: 3em;
  border-bottom: var(--gray) solid 1px;
}

@media screen and (min-width: 1400px) {
  #header {
    display: flex;
    flex-direction: row;
    padding-bottom: 3em;
  }

}

@media screen and (max-width: 1400px) {
  #header {
    flex-direction: column;
    align-items: center;
    padding-bottom: 3em;
    border-bottom: var(--gray) solid 1px;
    opacity: 1;
  }

}

@media screen and (max-width: 1000px) {
  #main-content-box {
    margin: 0;
  }

  h1 {
    font-size: 40pt;
    margin-top: 20px;
  }

  h3 {
    font-size: 14pt;
  }
}



#overview {
  text-align: left;
  margin-left: 1em;
}

#resume-container {
  justify-content: flex-end;
  display: flex;
}

#resume {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: var(--gray);
  font-weight: bold;
  align-items: center;
  width: fit-content;
}

.icon {
  width: 2em;
  margin: 0 auto;
}